@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: 'Roboto', sans-serif, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: hsl(222, 26%, 31%);
}

.section-center {
    width: 350px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: hsl(224, 36%, 15%);
}

.expression {
    font-size: .75rem;
    text-align: right;
    font-weight: bold;
    color: lightcoral;
    padding: 10px;
}

.displayCalc {
    background-color: hsl(224, 36%, 15%);
    padding: 10px;
    font-family: inherit;
    font-size: 1.72rem;
    font-weight: bold;
    text-align: right;
    color: #fff;
    letter-spacing: 4px;
}

.container {
    width: 100%;
    background-color: hsl(223, 31%, 20%);
    padding: 20px;
    border-radius: 5px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btns {
    /*  width: 65px;*/
    height: 50px;
    border-radius: 5px;
    color: hsl(221, 14%, 31%);
    background-color: hsl(30, 25%, 89%);
    box-shadow: 0px 2px 1px 1px hsl(28, 16%, 65%);
    font-family: inherit;
    font-weight: 700;
    font-size: 1rem;
    cursor: pointer;
    outline: none;
    border: none;
}

.grey-btn {
    background-color: hsl(225, 21%, 49%);
    box-shadow: 0px 2px 1px 1px hsl(224, 28%, 35%);
    color: #fff;
}

.red-btn {
    background-color: hsl(6, 63%, 50%);
    box-shadow: 0px 2px 1px 1px hsl(6, 70%, 34%);
    color: #fff;
}

#equals {
    grid-column-start: 1;
    grid-column-end: 3;
}

#clear {
    grid-column: 3 / 5;
}

.btns:active {
    -webkit-transform: scale(.9);
            transform: scale(.9);
}

.btns:hover {
    opacity: .8;
}
